.tracks-grid {
    display: grid;
    gap: 12px;
    padding: 6px 0;
    min-width: 0;
  }
  
  .track-card {
    position: relative;
    display: grid;
    grid-template-columns: auto minmax(0, 1fr) auto;
    align-items: center;
    gap: 14px;
    padding: 14px 16px;
    border-radius: 16px;
    text-decoration: none;
    isolation: isolate;
    background:
      radial-gradient(120% 120% at 0% 0%,
        rgba(20, 24, 28, 0.75) 0%,
        rgba(20, 24, 28, 0.95) 60%,
        rgba(20, 24, 28, 1) 100%);
    border: 1px solid rgba(255, 215, 0, 0.18); /* soft gold */
    box-shadow:
      0 1px 0 rgba(255, 255, 255, 0.04) inset,
      0 8px 24px rgba(0, 0, 0, 0.35);
    transition: transform 180ms ease, box-shadow 180ms ease, border-color 180ms ease;
    color: #eaeaf2; /* readable on dark */
    will-change: transform;
    max-width: 100%;
    overflow: hidden;
  }
  
  @media (prefers-color-scheme: light) {
    .track-card {
      background:
        radial-gradient(120% 120% at 0% 0%,
          rgba(250, 250, 255, 0.9) 0%,
          rgba(245, 247, 255, 0.95) 60%,
          #ffffff 100%);
      color: #1d2330;
      border-color: rgba(255, 215, 0, 0.28);
      box-shadow:
        0 1px 0 rgba(0, 0, 0, 0.03) inset,
        0 10px 30px rgba(26, 32, 44, 0.12);
    }
  }
  
  .track-card:hover {
    transform: translate3d(0, -2px, 0);
    border-color: rgba(255, 215, 0, 0.5);
    box-shadow:
      0 1px 0 rgba(255, 255, 255, 0.06) inset,
      0 16px 40px rgba(255, 105, 180, 0.18), /* soft pink glow */
      0 10px 26px rgba(0, 173, 181, 0.22);   /* pastel teal glow */
  }
  .track-card:focus-visible {
    outline: 3px solid rgba(255, 215, 0, 0.6);
    outline-offset: 3px;
  }
  
  .spark-ring {
    position: absolute;
    inset: -1px;
    border-radius: 16px;
    background:
      conic-gradient(from 140deg,
        rgba(0, 173, 181, 0.35),
        rgba(255, 105, 180, 0.35),
        rgba(255, 215, 0, 0.35),
        rgba(0, 173, 181, 0.35));
    filter: blur(16px);
    opacity: 0.15;
    z-index: -1;
    transition: opacity 180ms ease, filter 180ms ease;
  }
  .track-card:hover .spark-ring { opacity: 0.35; filter: blur(12px); }
  
  .bullet {
    width: 40px;
    height: 40px;
    border-radius: 12px;
    display: grid;
    place-items: center;
    background:
      radial-gradient(circle at 30% 20%, rgba(255,255,255,0.14), transparent 40%),
      linear-gradient(135deg, rgba(0,173,181,0.25), rgba(255,105,180,0.25));
    border: 1px solid rgba(255, 215, 0, 0.28);
    box-shadow: 0 6px 16px rgba(0, 0, 0, 0.25) inset;
  }
  
  .spot-glyph {
    width: 18px;
    height: 18px;
    stroke: currentColor;
    fill: none;
    stroke-width: 1.6;
    opacity: 0.9;
  }
  
  .track-copy strong {
    font-weight: 700;
    letter-spacing: 0.2px;
    color: #ffd700cc; /* gold accent */
  }
  
  .title-line small {
    font-size: 0.95rem;
    opacity: 0.95;
    word-break: break-word;
  }
  
  .go-chip {
    font-size: 0.8rem;
    padding: 6px 10px;
    border-radius: 999px;
    border: 1px dashed rgba(0, 173, 181, 0.5);
    background: linear-gradient(90deg, rgba(0,173,181,0.12), rgba(255,105,180,0.12));
    white-space: nowrap;
    transform: translateZ(0);
    transition: border-color 180ms ease, background 180ms ease, letter-spacing 180ms ease;
    max-width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .track-card:hover .go-chip {
    border-color: rgba(255, 215, 0, 0.7);
    background: linear-gradient(90deg, rgba(255,105,180,0.18), rgba(0,173,181,0.18));
    letter-spacing: 0.2px;
  }
  
  /* Optional: compact mode on small widths */
  @media (max-width: 520px) {
    .track-card {
      grid-template-columns: auto 1fr;
      grid-auto-flow: row;
      gap: 10px;
    }
    .go-chip { grid-column: 1 / -1; justify-self: flex-start; }
  }
  /* Scrollable container so the card never grows unbounded */
.tracks-scroll {
    width: 100%;
    max-height: none;
    overflow: visible;
    padding: 8px 8px 12px 8px; /* breathing room so hover lifts never clip */
  }
  
  /* Subtle scrollbar so it looks intentional */
  .tracks-scroll::-webkit-scrollbar { width: 8px; }
  .tracks-scroll::-webkit-scrollbar-track {
    background: transparent;
  }
  .tracks-scroll::-webkit-scrollbar-thumb {
    background: color-mix(in srgb, #ffd700, #000 70%);
    border-radius: 8px;
  }
  .tracks-scroll::-webkit-scrollbar-thumb:hover {
    background: color-mix(in srgb, #ffd700, #000 55%);
  }
  
  /* Loading + empty states */
  .tracks-fallback,
  .tracks-empty {
    color: #aab;
    font-size: 0.95rem;
    padding: 8px 4px;
  }
  
  .tracks-skel .shimmer {
    height: 56px;
    border-radius: 14px;
    margin-bottom: 12px;
    background:
      linear-gradient(90deg,
        rgba(255,255,255,0.06) 0%,
        rgba(255,255,255,0.14) 40%,
        rgba(255,255,255,0.06) 80%);
    animation: shimmer 1.2s infinite;
    border: 1px solid rgba(255,255,255,0.08);
  }
  @keyframes shimmer {
    0% { background-position: -200px 0; }
    100% { background-position: 200px 0; }
  }
  
  
